<template>

    <div>
        <header>
            <button @click="back()" class="btn">返回</button>
            <span>收益率曲线</span>
        </header>
         <section>
            <ul>
                <li class="echatrs-one">
                    <div id="echatrs-one">
                    </div>
                </li>
                <li class="echatrs-two">
                        <div id="echatrs-two">
                        </div>
                </li>
                <li class="echatrs-three">
                        <div id="echatrs-three">
                        </div>
                </li>
            </ul>
        </section>
         <div class="bot"></div>
    </div>
</template>

<script>
export default {
  name: 'curvevienew',
  data(){
    return {
    }
  },
  created(){
      this.$store.commit("changeAll")
  },
  methods:{
      back(){
          this.$router.go(-1)
        }
    },
   mounted(){
    console.log(this)
    //ftp曲线
    var echartsOne = this.$echarts.init(document.getElementById('echatrs-one'));
    //SHIBOR曲线
    var echartsTwo = this.$echarts.init(document.getElementById('echatrs-two'));
    //银行普通债曲线
    var echartsThree =this.$echarts.init(document.getElementById('echatrs-three')); 

    var optionOne = {
        backgroundColor:'#0f375f',
        title : {
            text: 'FTP收益率曲线(AA)',
            //subtext: '纯属虚构',
            x:'center',
            textStyle: {
                fontWeight: 'normal',  
                fontSize:'14',
                color:'#fff'
	      	}
        },                
        tooltip: {
            trigger: 'item',
            axisPointer: {
                type: 'cross'
            }
        },
        xAxis: {
            type: 'category',
            data: ['1天', '7天', '14天', '1月', '3月', '6月', '9月','1年','3年','5年','10年','15年'],
            axisLabel: {        
                show: true, 
                interval: 0  ,   //这行代码控制着坐标轴x轴的文字是否显示
                  textStyle: {
                      color: '#fff',   //x轴上的字体颜色
                      fontSize:'12'    // x轴字体大小
                  }
              },
              axisLine: {
                  lineStyle: {
                      color: '#ccc'
                  }
              }
        },
        yAxis: {
            type: 'value',
            axisLabel: {        
                show: true, 
                interval: 0 
              },
              axisLine: {
                  lineStyle: {
                      color: '#ccc'
                  }
              }
        },
        series: [{
            name:'利率',
            data: [2.72, 2.91, 3.81,3.78, 3.98,4.18,4.31,4.38,4.8,5.02,5.35,5.45,5.63],
            type: 'line',
            symbolSize: 12,
            smooth: true,
            itemStyle : {  
                normal : { 
                    color:'#00B0F0'
                }  
            },
            lineStyle:{  
                normal : { 
                    color:'#00B0F0'
                }   
            }  
            
        }]
    };
    echartsOne.setOption(optionOne)   
    var data = [2.72, 2.91, 3.81,3.78, 3.98,4.18,4.31,4.38];
    var links = data.map(function (item, i) {
        return {
            source: i,
            target: i + 1
        };
    });
    links.pop();
    var optionTwo = {
        title: {
            text: 'SHIBOR收益率曲线',
            x:'center',
            textStyle: {
                fontWeight: 'normal',  
                fontSize:'14',
                color:'#fff'
	      	}
        },        
        backgroundColor: '#0f375f',
        tooltip: {
            trigger: 'item',
            axisPointer: {
                type: 'shadow'
            }
        },
        xAxis: {
            type : 'category',
            boundaryGap : false,
            data : ['1天', '7天', '14天', '1月', '3月', '6月', '9月','1年'],
            axisLabel: {        
                show: true, 
                interval: 0,   //这行代码控制着坐标轴x轴的文字是否显示
                    textStyle: {
                        color: '#fff',   //x轴上的字体颜色
                        fontSize:'12'    // x轴字体大小
                    }
            },
            axisLine: {
                lineStyle: {
                    color: '#fff'
                }
            }
        },
        yAxis: {
            type : 'value',
            axisLine: {
                lineStyle: {
                    color: '#fff'
                }
            }
        },
        series: [
            {
                name:'利率',
                type: 'graph',
                layout: 'none',
                coordinateSystem: 'cartesian2d',
                symbolSize: 12,
                label: {
                    normal: {
                        show: false
                    }
                },
                edgeSymbol: ['circle', 'arrow'],
                edgeSymbolSize: [4, 10],
                data: data,
                links: links,
                lineStyle: { 
                    normal: {
                        color: '#fff'
                    }
                },
                itemStyle : {
                    normal : {
                        color:'#00B0F0'
                    }
                }
            }
        ]
    };
    echartsTwo.setOption(optionTwo)  
    var optionThree =  {
        title : {
            text: '银行普通债收益率曲线(AA)(到期)',
            //subtext: '纯属虚构',
            x:'center',
            textStyle: {
                fontWeight: 'normal',  
                fontSize:'14',
                color:'#fff'
	      	}
        },
        backgroundColor: '#0f375f',
        tooltip: {
            trigger: 'item',
            axisPointer: {
                type: 'shadow'
            }
        },
        legend: {
            data: ['line', 'bar'],
            textStyle: {
                color: '#ff0'
            },
            x:'right',
            y:20
        },
        xAxis: {
            data: ['0.0Y','0.02Y','0.04Y','0.08Y','0.17Y','0.25Y','0.5Y','0.75Y','1.0Y'],
            axisLabel: {        
                show: true, 
                interval: 0  ,   //这行代码控制着坐标轴x轴的文字是否显示
                textStyle: {
                      color: '#fff',   //x轴上的字体颜色
                      fontSize:'12'    // x轴字体大小
                }
              },
            axisLine: {
                lineStyle: {
                    color: '#fff'
                }
            }
        },
        yAxis: {
            splitLine: {show: false},
            axisLine: {
                lineStyle: {
                    color: '#fff'
                }
            }
        },
        series: [{
            name: '利率',
            type: 'line',
            smooth: false,
            showAllSymbol: true,
            symbol: 'emptyCircle',
            symbolSize: 10,
            lineStyle:{
                color:'#fff'
            },
            data: [4.36, 4.84, 4.54,4.39,4.50,4.49,4.54,4.60,4.64],
            
        }, {
            type: 'bar',
            barWidth: 5,
            showSymbol: false,
            itemStyle: {
                normal: {
                    barBorderRadius: 5,
                    color: "#00B0F0",
                    label: {
                        show: false
                    },
                    fontSize:20
                }
            },
            data:  [4.36, 4.84, 4.54,4.39,4.50,4.49,4.54,4.60,4.64]
        }]
    };
    echartsThree.setOption(optionThree);  
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
   header{
        width: 100%;
        height: 2rem;
        background-color: #4674c1;
        line-height:2rem;
        text-align: center;
        color: #fff;
        margin-bottom: 0.2rem;
        font-size: 18px;
        overflow: hidden;
        position: relative;        
        vertical-align: middle;
    }
    button{
        position: absolute;
        left: 1rem;
        top: 0.1rem;
        text-align: center;
        line-height: 100%;
        background-color: #6cf;
    }
    section{
        width: 100%;
        background-color: #ebebeb
    }
    section ul{
        padding: 0.5rem 0.5rem 0 0.5rem;
    
    }
    section li{
        width: 100%;
        height: 20rem;
        text-align: center; 
        margin-bottom: 1.5rem;   
    }
    section li div{
        width: 100%;
        height: 100%;
    }
    section ul .echatrs-one{    
        background-color: #6cf;
    }
    section ul .echatrs-two{    
        background-color: #73ac4d;
    }
    section ul .echatrs-three{    
        background-color: #ea7d3c;
    }
    #echatrs-one .change,#echatrs-two .change,#echatrs-three .change{
        position: absolute;
        left: -0.3rem!important;
        top: -2rem!important;
        width: 100%!important;
        height: 20%!important;
    }
    #curve-ul li div .change{
        position: absolute;
        left: -0.3rem!important;
        top: -2rem!important;
        width: 100%!important;
        height: 20%!important;
    }
    .bot{
       width: 100%;
       height: 4rem;
    }
</style>




