<template>
    <div class="box">
        <div>
                <img src="../../assets/1.png" alt="">
                <p>中央结算公司编制的中债国债及其他债券收益率曲线在中国银保监会网站发布</p>
        </div>
    </div>
</template>

<script>
export default {
  name: 'vswriper'

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .box{
        width: 100%;
        text-align: center;
        margin-bottom: 1rem;
        background-color: #ebebeb;
        padding:0.5rem 0.5rem 0 0.5rem;
        background-size: contain;
        box-sizing: border-box;
        font-size: 14px;
    }
    .box div{  
        width: 100%;
        height: 10rem;
        position: relative;

    }
    .box img {
        width: 100%;
        height: 100%;        
        border-radius: 0.8rem 0.8rem 0 0;
        position: absolute;
        left: 0;
        top: 0;
    }
    .box p{
        position: absolute;
        left: 0;
        bottom: 0;
        margin: 0.2rem 2.4rem;
         color: #fff;
    }
</style>



