<template>
    <div class="vfotter">
        <ul class="bot-ul">
            <li class="bot-li">
                <div class="bottom-list" v-show="showFir">
                    <ul>
                        <li>
                         <router-link tag="p" :to="{name:'homeLink'}">公司介绍</router-link>
                         </li>
                        <li>公司资质</li>
                        <li>产品服务</li>
                        <li>客户案例</li>
                        <li style="border-bottom:none">联系我们</li>
                    </ul> 
                    <div></div>   
                </div>                
                <button class="btn btn-primary" @click="changeFir()">关于金实</button>                
            </li>
            <li class="bot-li">
                 <div class="bottom-list" v-show="showMid">
                    <ul>
                        <li>
                            <router-link tag="p" :to="{name:'macrLink'}">
                                宏观分析                                
                            </router-link>
                        </li>
                        <!-- <li>
                            <router-link tag="p" :to="{name:'curvLink'}">
                                收益曲线                                
                            </router-link>
                        </li> -->
                        <li>
                            <router-link tag="p" :to="{name:'echatrsmouduleLink'}">
                                收益曲线                                
                            </router-link>
                        </li>
                        <li>服务公告</li>
                        <li>在线留言</li>
                        <li style="border-bottom:none">常见问题</li>
                    </ul> 
                    <div></div>   
                </div>           
                <button class="btn btn-primary" v-on:click="changeMid()" >公告服务</button>  
            </li>
           
            <li class="bot-li">
                <div class="bottom-list" v-show="showTh">
                    <ul>
                        <li>会员认证</li>
                        <li>同业交流</li>
                        <li>数据下载</li>
                        <li>指标分析</li>
                        <li style="border-bottom:none">分析报告</li>
                    </ul> 
                    <div></div>   
                </div>                
                <button class="btn btn-primary" v-on:click="changeTh()">增值服务</button>  
            </li>
        </ul>
    </div>
</template>

<script>
export default {
  name: 'vfotter',
  data () {
      return {
      }
  },
  computed:{      
    showMid:{
       get(){
            return this.$store.getters.isshowMid
       },
       set(){

       }
    },
     showFir:{
       get(){
            return this.$store.getters.isshowFir
       },
       set(){

       }
    },    
     showTh:{
       get(){
            return this.$store.getters.isshowTh
       },
       set(){

       }
    }
    
  },
  methods: {
      changeMid(){
          this.$store.commit("changeState")
      },
      changeFir(){
          this.$store.commit("changeFir")
      },      
      changeTh(){
          this.$store.commit("changeTh")
      }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .vfotter{
        border-top: none;
        position: fixed;
        bottom: 0;
        left: 0; 
        width: 100%;
        background-color: #ebebeb;
        height: 3rem;
        line-height: 3rem;
    }
    .vfotter .bot-ul{
        display: flex;
    }
    .vfotter .bot-ul .bot-li{
        flex: 1;
        position: relative;
    }
    .bottom-list{
        width: 78%;
        border-radius: 10px;        
        background-color: #6cf;
        color: #000;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        margin:0 auto;
        margin-bottom: 3.4rem;
    }
    .bottom-list ul{
        padding: 0.4rem 1.2rem 0 1.2rem;
    }
    .bottom-list div{
        width: 0;
        height: 0;
        border-left: 0.4rem solid transparent;
        border-right: 0.4rem solid transparent;
        border-top: 0.8rem solid #6cf;
        position: absolute;
        bottom: -0.6rem;
        right: 1rem;
    }
    .bottom-list li{
        border-bottom: 1px dashed #999;
    }

</style>



