<template>
    <div class="box">  
        <header>金实智能司库</header>    
        <vSwriper></vSwriper>    
        <vList></vList>
    </div>
</template>

<script>
import vSwriper from './vSwriper'
import vList from './vList'
export default {
  name: 'home',
  components: {
      vSwriper,
      vList
  },  
  created(){
       this.$store.commit("changeAll")
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    header{
        width: 100%;
        height: 2rem;
        background-color: #4674c1;
        line-height:2rem;
        text-align: center;
        color: #fff;
        margin-bottom: 0.2rem;
        font-size: 18px;        
        vertical-align: middle;
    }
</style>



