
import CurveView from './components/curve/CurveView'
import Home from './components/home/Home'
import MacroAnalysis from './components/macroAnalysis/MacroAnalysis'
import ListDetail from './components/home/ListDetail'
import Echatrsmoudule from './components/curve/Echatrsmoudule'

export default [
    {path:"/",name:'homeLink',component:Home},
    {path:"/curveview",name:'curvLink',component:CurveView},
    {path:"/echatrsmoudule",name:'echatrsmouduleLink',component:Echatrsmoudule},
    {path:"/macroanalysis",name:'macrLink',component:MacroAnalysis},
    {path:"/home/listdetail",name:'listLink',component:ListDetail},
    {path:"*",redirect:'/'}
]


