import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export const store = new Vuex.Store({
    state:{
        // 设置属性
        showFir:true,
        showMid:false,
        showTh:false,
      },
      getters:{
        // 获取属性的状态
        isshowMid:state => state.showMid,
        isshowFir:state => state.showFir,
        isshowTh:state => state.showTh
      },
      mutations:{
        // 改变属性的状态
        changeState(state){
          state.showMid =  !state.showMid
          state.showFir = false
          state.showTh = false
        },
        changeFir(state){
          state.showFir =  !state.showFir
          state.showTh = false
          state.showMid = false
        },        
        changeTh(state){
          state.showTh =  !state.showTh
          state.showMid = false
          state.showFir = false
        },
        changeAll(state){
          state.showMid = false
          state.showFir = false
          state.showTh =  false
        }
      }
})


