<template>

    <div>
        <header>
            <button @click="back()" class="btn">返回</button>
            <span>收益率曲线</span>
        </header>
        <section>
            <ul id="curve-ul">
                <li v-for = "(item,index) in itemlist" :key="index" :style="{backgroundColor:arrColor[index%3]}">
                    <div :id="getId(index)">
                    </div>
                </li>
            </ul>
        </section>
         <div class="bot"></div>
    </div>
</template>

<script>
export default {
  name: 'curveview',
  data(){
    return {
        itemlist:[],
        echartsClass:'change',
        arrColor:['#6cf','#73ac4d','#ea7d3c']
    }
  },
  methods: {
        back(){
          this.$router.go(-1)
        },      
        getId: function (index){
            return "echarts" +(index+1)
        }
  },
  created(){
        this.$store.commit("changeAll")
  },
  mounted () {    
            this.$Axios({
            method:'get',
            url:'http://localhost:8080/static/data.json'
            })
            .then((res) =>{
                var num =0 ;
                var data = res.data.data  
                this.itemlist = data                           
                return data    
            })
            .then((data)=>{
                    this.$nextTick(function(){        
                        var num =0;         
                        for(var i=0;i<data.length;i++){  
                            num++
                            var echartsInit =this.$echarts.init(document.getElementById('echarts'+num+''));
                            var echatsX = data[i].echartsX.split('#');                            
                            var echartData =data[i].echartsData.split('#');
                            var echartTitle = data[i].title; 
                            var option = {
                                title : {
                                    text: echartTitle,
                                    //subtext: '纯属虚构',
                                    x:'center',
                                    textStyle: {
                                        fontWeight: 'normal',  
                                        fontSize:'14'
                                    }
                                },                
                                tooltip: {
                                    trigger: 'axis',
                                    axisPointer: {
                                        type: 'cross'
                                    }
                                },
                                xAxis: {
                                    type: 'category',
                                    data: echatsX,
                                    axisLabel: {        
                                        show: true, 
                                        interval: 0  ,   //这行代码控制着坐标轴x轴的文字是否显示
                                        textStyle: {
                                            color: 'red',   //x轴上的字体颜色
                                            fontSize:'14'    // x轴字体大小
                                        }
                                    }
                                },
                                yAxis: {
                                    type: 'value',
                                    axisLabel: {        
                                        show: true, 
                                        interval: 0 
                                    }
                                },
                                series: [{
                                    name:'名字',
                                    data: echartData,
                                    type: 'line',
                                    smooth: true,
                                    itemStyle : {  
                                        normal : {  
                                            lineStyle:{  
                                                color:'#000'  
                                            }  
                                        }  
                                    }
                                }]
                            };
                            echartsInit.setOption(option);
                            // $("#echatrs-"+num).find("div").eq(1).addClass('change'); 
                            // var arrColor = ['#6cf','#73ac4d','#ea7d3c']
                            // $("#echatrs-"+num).parent().css('background-color',arrColor[i%3])   

                        }
                        
                    })
                }

            )
            .catch(function(){
            alert('网络错误')
            });
             
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
   header{
        width: 100%;
        height: 2rem;
        background-color: #4674c1;
        line-height:2rem;
        text-align: center;
        color: #fff;
        margin-bottom: 0.2rem;
        font-size: 18px;
        overflow: hidden;
        position: relative;        
        vertical-align: middle;
    }
    button{
        position: absolute;
        left: 1rem;
        top: 0.1rem;
        text-align: center;
        line-height: 100%;
        background-color: #6cf;
    }
    section{
    width: 100%;
    background-color: #ebebeb;
     padding: 0.5rem 0.5rem 0 0.5rem;
}
section ul{
   background-color: #fff;
     
}
section li{
    width: 100%;
    height: 20rem;
    text-align: center; 
    margin-bottom: 1.5rem; 
      
}
section li div{
    width: 100%;
    height: 100%;
}
section ul .echatrs-one{    
    background-color: #6cf;
}
section ul .echatrs-two{    
    background-color: #73ac4d;
}
section ul .echatrs-three{    
    background-color: #ea7d3c;
}
/* #echatrs-one .change,#echatrs-two .change,#echatrs-three .change{
    position: absolute;
    left: -0.3rem!important;
    top: -2rem!important;
    width: 100%!important;
    height: 20%!important;
} */
#curve-ul li div .change{
    position: absolute;
    left: -0.3rem!important;
    top: -2rem!important;
    width: 100%!important;
    height: 20%!important;
}
.bot{
       width: 100%;
       height: 4rem;
   }
</style>



